import * as React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import { ActionCreator } from '../../store/action';
import MainNav from './MainNav/MainNav';
import './Header.scss';
import { AppUrl } from '../../consts';
var Header = function () {
    var username = useSelector(function (state) { return state.user.username; });
    var dispatch = useDispatch();
    var navigate = useNavigate();
    var onExitButtonClick = function () {
        dispatch(ActionCreator.getUsername(null)); // удаляем имя пользователя
        dispatch(ActionCreator.updateAuthStatus(false)); // удаляем статус аутентификации
        navigate(AppUrl.LOGIN);
    };
    return (React.createElement("header", { className: "header" },
        React.createElement("div", { className: "header__logo" },
            React.createElement("img", { className: 'header__logo-image', src: require('../../img/logo.png'), alt: "\u043B\u043E\u0433\u043E\u0442\u0438\u043F Squeeze.me" })),
        React.createElement("div", { className: "header__nav" },
            React.createElement(MainNav, null)),
        React.createElement("div", { className: "header__user-menu" },
            React.createElement("p", { className: "header__username" }, username),
            React.createElement("button", { className: "header__exit-button", onClick: onExitButtonClick }, "\u0412\u044B\u0439\u0442\u0438"))));
};
export default Header;
